<template>
  <v-container fluid class="mt-3">
    <v-row>
      <v-col cols="12">
        <v-card>
          <v-card-title class="text-h5 mb-3"></v-card-title>
          <v-card-text>
            <v-form ref="form">
              <v-row>
                <v-col cols="12" sm="7">
                  <v-text-field
                    v-model="articleInfo.title"
                    label=""
                    autocomplete="off"
                    outlined
                    dense
                  ></v-text-field>

                  <v-file-input
                    accept="image/*"
                    label=""
                    v-model="uploadFile"
                    class="my-5"
                    outlined
                    dense
                  ></v-file-input>

                  <v-select
                    v-model="articleInfo.tags"
                    :items="tagsSelect"
                    :rules="tagRules"
                    label=""
                    multiple
                    chips
                    persistent-hint
                    outlined
                  ></v-select>
                </v-col>
                <v-col cols="12" sm="5">
                  <v-img :src="articleInfo.avatar_url"></v-img>
                </v-col>
              </v-row>

              <v-row>
                <v-col cols="24">
                  <vue-editor
                    v-model="articleInfo.content"
                    placeholder=""
                  ></vue-editor>
                </v-col>
              </v-row>

              <v-row>
                <v-col cols="12" sm="6">
                  <v-btn color="success" class="mr-4 mt-4" @click="submitForm">
                    
                  </v-btn>
                </v-col>
              </v-row>
            </v-form>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { FETCH_TAG_LIST } from "@/store/actionTypes";
import { VueEditor } from "vue2-editor";
import { mapState, mapActions } from "vuex";
import { createArticle } from "@/api/index";

export default {
  name: "ArticleAdd",
  components: {
    VueEditor,
  },
  data: () => ({
    articleInfo: {
      title: "",
      content: "",
      avatar_url: "",
      tags: [],
    },
    uploadFile: null,
    tagRules: [
      (v) => !!v.length || "",
      (v) => v.length <= 4 || "4",
    ],
  }),
  computed: {
    ...mapState(["tagList"]),
    tagsSelect() {
      return this.tagList.map((tag) => ({
        text: tag.name,
        value: tag._id,
      }));
    },
  },
  watch: {
    uploadFile(newValue) {
      if (newValue) {
        this.previewImageBeforeUpload(newValue, (dataURL) => {
          this.articleInfo.avatar_url = dataURL;
        });
      } else {
        this.articleInfo.avatar_url = "";
      }
    },
  },
  methods: {
    ...mapActions([FETCH_TAG_LIST]),
    async submitForm() {
      const passed = this.$refs.form.validate();
      if (passed) {
        if (this.uploadFile) {
          try {
            const { data } = await this.uploadImg(this.uploadFile);
            this.articleInfo.avatar_url = data.url;
          } catch (error) {
            //
          }
        }

        try {
          await createArticle(this.articleInfo);
          this.$message.success("");
          this.$router.push("/article/list");
        } catch (err) {
          //
        }
      } else {
        this.$message.error("");
      }
    },
  },
  created() {
    this.FETCH_TAG_LIST({
      page: 1,
      per_page: 100,
    });
  },
};
</script>

